* {
  font-family: 'Roboto', sans-serif; }

nav {
  position: fixed;
  width: 100%;
  height: 53px;
  background: #212121;
  z-index: 1; }

header {
  position: relative; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 14px 70px;
  margin: 0;
  list-style: none;

  .menu-link {
    color: #FFFFFF;
    font-weight: 300;
    font-size: 15px; }

  .menu-item {
    border-right: 2px dotted black;
    padding-right: 40px; } }

.subheader {
  transform: translateY(53px);
  position: absolute;
  width: 100%;
  height: 87px;
  background: rgba(0, 0, 0, 0.6); }

.subheader-logo {
  display: block;
  max-width: 100%;
  padding-top: 16px; }

.subheader {
  .subheader-logo-text {
    margin-top: 6px;
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.6; }

  .subheader-call {
    margin-top: 17px;
    text-align: right;
    font-weight: 300;
    font-size: 12px;
    color: #FFFFFF; }

  .subheader-call-number {
    display: block;
    font-weight: 400;
    font-size: 22px;
    text-align: right;
    text-transform: uppercase;
    color: #FFFFFF; }

  .subheader-btn {
    margin-top: 21px;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 8px;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    text-align: center; } }

.promo {
  min-height: 800px;
  background: url('../Img/uber_bg.png') center (center / cover) no-repeat;

  .promo-header {
    margin-top: 151px;
    font-weight: 900;
    font-size: 60px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-align: center; }

  .promo-subheader {
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-align: center; }

  .promo-descr {
    margin-top: 16px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF; }

  .promo-btn {
    margin: 0 auto;
    margin-top: 115px;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.reason {
  min-height: 850px;
  margin: 18px 0 64px 0;

  .reason-benefits {
    display: block;
    margin: 0 auto;
    padding-top: 6px;
    width: 115px;
    height: 27px;
    background: #000000;
    font-weight: 400;
    font-size: 11px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF; }

  .reason-title {
    margin-top: 32px;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222222; }

  .reason-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 26px;

    .reason-round {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 116px;
      height: 116px;
      background: #1EACC7;
      border-radius: 100%;
      margin-right: 26px; }

    .reason-item-text {
      width: 372px;

      .reason-item-title {
        font-weight: 400;
        font-size: 17px;
        line-height: 21px;
        color: #1EACC7; }

      .reason-item-descr {
        font-weight: 300;
        font-size: 15px;
        line-height: 21px;
        color: #202020; } } } }
